h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  display: block;
}

ul {
  list-style: none;
}

a {
  text-align: center;
  color: #dbd7d7;
  padding: 0 0 0 20px;
  text-decoration: none;
}

body {
  color: #dbd7d7;
  background-color: #22232b;
  padding-top: 20px;
}

.timer {
  background-color: #202020;
  padding: 20px;
}

.timer__blocks-list {
  width: 80px;
  color: #ddd;
  background-color: #4b4b4b;
  justify-content: center;
  gap: 10px;
  padding: 5px;
  font-size: 20px;
  display: flex;
}

.timer__items--rigth-border {
  border-right: 1px solid #db6312;
  padding-right: 10px;
}

.form {
  background-color: #32343b;
  flex-direction: row;
  padding: 20px;
  display: flex;
  position: relative;
}

.form__input {
  min-width: 190px;
  color: inherit;
  background-color: #585a63;
  border: none;
  border-radius: 10px;
  padding: 10px;
}

.close-mobile {
  width: 15px;
  height: 15px;
  fill: #e4e4e4;
  display: inline-block;
}

.form__reset-btn {
  background-color: #e2580a;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 20px;
  left: 170px;
}

